import { IonAccordion, IonAccordionGroup, IonAvatar, IonButton, IonCard, IonCardContent, IonCardSubtitle, IonCardTitle, IonChip, IonCol, IonContent, IonFab, IonFabButton, IonHeader, IonIcon, IonImg, IonItem, IonLabel, IonList, IonListHeader, IonPage, IonRow, IonText, IonTitle, IonToggle, IonToolbar } from '@ionic/react';
import { body, caretDown, fitness, heartDislike, pencil } from 'ionicons/icons';
import ExploreContainer from '../components/ExploreContainer';
import './Tab3.css';

const Tab3: React.FC = () => {
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle size='small'>Profile</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse='condense'>
          <IonToolbar>
            <IonTitle size="large">Profile</IonTitle>
          </IonToolbar>
        </IonHeader>

        <div className='container'>
          <div className='first'>
            <IonCard class='profile-card'>
              <IonRow>
                <IonCol>
                  <div className='img-container'>
                    <IonAvatar class='profile-img'>
                      <img src="assets/anteater.png"/>
                    </IonAvatar>
                  </div>
                </IonCol>
                <IonCol>
                  <IonCardTitle class='name'>Peter Anteater</IonCardTitle>
                </IonCol>
              </IonRow>
            </IonCard>
          </div>
        </div>

        <div className='container'>
          <IonCard class='profile-card'>
            <IonItem>
              <IonLabel>Use my physical activity</IonLabel>
              <IonToggle slot='end'></IonToggle>
            </IonItem>
          </IonCard>
        </div>

        <div className='container'>
          <IonCard class='profile-card'>
            <IonItem>
              <IonLabel>Use my location</IonLabel>
              <IonToggle slot='end'></IonToggle>
            </IonItem>
          </IonCard>
        </div>

        <div className='container'>
          <IonCard class='profile-card'>
            <IonItem>
              <IonLabel>Use my weather</IonLabel>
              <IonToggle slot='end'></IonToggle>
            </IonItem>
          </IonCard>
        </div>
   
        <div className='accordion'>
          <IonAccordionGroup expand='inset'>
            <IonAccordion value='first'>
              <IonItem slot='header'>
                <IonRow>
                  <IonIcon class='body-icon' icon={body}/>
                  <IonCardSubtitle class='basic-info'>BASIC INFO</IonCardSubtitle>
                </IonRow>
              </IonItem>
              <div className='basic-info-content' slot='content'>
                <IonRow>
                  <IonCol>Age:</IonCol>
                  <IonCol>21</IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>Sex:</IonCol>
                  <IonCol>Male</IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>Height:</IonCol>
                  <IonCol>6'7</IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>Weight:</IonCol>
                  <IonCol>200lbs</IonCol>
                  <IonCol>
                    <div className='edit-container'>
                      <IonFabButton class='edit-button'>
                        <IonIcon class='edit-icon' icon={pencil}></IonIcon>
                      </IonFabButton>
                    </div>
                  </IonCol>
                </IonRow>
              </div>
            </IonAccordion>
          </IonAccordionGroup>
        </div>

        <div className='accordion'>
          <IonAccordionGroup expand='inset'>
            <IonAccordion value='first'>
              <IonItem slot='header'>
                <IonRow>
                  <IonIcon class='body-icon' icon={fitness}/>
                  <IonCardSubtitle class='basic-info'>HEALTH GOALS</IonCardSubtitle>
                </IonRow>
              </IonItem>
              <div className='basic-info-content' slot='content'>
                <IonRow>
                  <IonCol>Calories:</IonCol>
                  <IonCol>3000</IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>Carbs:</IonCol>
                  <IonCol>450</IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>Protein:</IonCol>
                  <IonCol>220</IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol>Fat:</IonCol>
                  <IonCol>100</IonCol>
                  <IonCol>
                    <div className='edit-container'>
                      <IonFabButton class='edit-button'>
                        <IonIcon class='edit-icon' icon={pencil}></IonIcon>
                      </IonFabButton>
                    </div>
                  </IonCol>
                </IonRow>
              </div>
            </IonAccordion>
          </IonAccordionGroup>
        </div>

        <div className='accordion'>
          <IonAccordionGroup expand='inset'>
            <IonAccordion value='first'>
              <IonItem slot='header'>
                <IonRow>
                  <IonIcon class='body-icon' icon={heartDislike}/>
                  <IonCardSubtitle class='basic-info'>RESTRICTIONS</IonCardSubtitle>
                </IonRow>
              </IonItem>
              <div className='restrictions-content' slot='content'>
                <IonRow>
                  <IonText class='restrictions-header'>
                    Allergies
                  </IonText>
                </IonRow>
                <IonRow class='restrictions-section'>
                  <IonCol>
                    <IonChip class='restrictions-item'>Peanuts</IonChip>
                  </IonCol>
                  <IonCol>
                    <IonChip class='restrictions-item'>Shellfish</IonChip>
                  </IonCol>
                  <IonCol></IonCol>
                  <IonCol></IonCol>
                </IonRow>
                <IonRow>
                  <IonText class='restrictions-header'>
                    Special Diets
                  </IonText>
                </IonRow>
                <IonRow class='restrictions-section'>
                  <IonCol>
                    <IonChip class='restrictions-item'>Vegetarian</IonChip>
                  </IonCol>
                </IonRow>
                <IonRow>
                  <IonText class='restrictions-header'>
                    Other
                  </IonText>
                </IonRow>
                <IonRow class='restrictions-section'>
                  <IonCol>
                    <IonChip class='restrictions-item'>Halal</IonChip>
                  </IonCol>
                  <IonCol>
                    <IonChip class='restrictions-item'>No Raw</IonChip>
                  </IonCol>
                  <IonCol></IonCol>
                  <IonCol>
                    <div className='edit-container-2'>
                      <IonFabButton class='edit-button'>
                        <IonIcon class='edit-icon' icon={pencil}></IonIcon>
                      </IonFabButton>
                    </div>
                  </IonCol>
                </IonRow>
              </div>
            </IonAccordion>
          </IonAccordionGroup>
        </div>

      </IonContent>
    </IonPage>
  );
};

export default Tab3;
