import { Health, HealthData } from '@ionic-native/health';
import { IonButton, IonCard, IonCardContent, IonCardHeader, IonCardSubtitle, IonCardTitle, IonCol, IonContent, IonFab, IonFabButton, IonHeader, IonIcon, IonLabel, IonPage, IonRow, IonSelect, IonSelectOption, IonTitle, IonToolbar } from '@ionic/react';
import { body } from 'ionicons/icons';
import { SetStateAction, useEffect, useState } from 'react';
import ExploreContainer from '../components/ExploreContainer';
import './Tab2.css';
import { Stepcounter } from '@ionic-native/stepcounter';



const Tab2: React.FC = () => {
  const [stepCount, setStepCount] = useState<number>(0);

  useEffect(() => {
    // Check if Stepcounter plugin is available
    if (typeof Stepcounter === 'undefined') {
      console.log('Stepcounter plugin not available');
      return;
    }

    const startStepCounter = () => {
      Stepcounter.start(stepCount).then(onSuccess => console.log('stepcounter-start success', onSuccess), onFailure => console.log('stepcounter-start error', onFailure));
    };

    // Stop the step counter
    const stopStepCounter = () => {
      Stepcounter.stop();
    };

    startStepCounter();

    // Cleanup function
    return () => {
      stopStepCounter();
    };
  }, []);


  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle size='small'>Meal</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Meal</IonTitle>
          </IonToolbar>
        </IonHeader>

        <div className='meal-num'>
          <IonRow>
            <IonLabel class='meal-num-text'>Today, I'm eating</IonLabel>
            <IonSelect interface='popover' placeholder='Select meals'>
              <IonSelectOption value='1'>1 meal</IonSelectOption>
              <IonSelectOption value='2'>2 meals</IonSelectOption>
              <IonSelectOption value='3'>3 meals</IonSelectOption>
              <IonSelectOption value='4'>4 meals</IonSelectOption>
              <IonSelectOption value='5+'>5+ meals</IonSelectOption>
            </IonSelect>
          </IonRow>
        </div>

        <div className='meal-button'>
          <IonButton size='large'>Build a Meal</IonButton>
        </div>
        
        <div className='results'>
          <IonCard>
            <img src="assets/chicken.jpg"/>
            <IonCardHeader>
              <IonCardTitle class='results-title'>Grilled Chicken</IonCardTitle>
            </IonCardHeader>
            <IonCardContent class='results-item'>
              300 Calories | 0g Carbs | 40g Protein | 15g Fat
            </IonCardContent>
          </IonCard>
        </div>
        <div className='results'>
          <IonCard>
            <img src="assets/egg.jpg"/>
            <IonCardHeader>
              <IonCardTitle class='results-title'>Egg Toast</IonCardTitle>
            </IonCardHeader>
            <IonCardContent class='results-item'>
              380 Calories | 15g Carbs | 9g Protein| 12g Fat
        </IonCardContent>
      </IonCard>
    </div>
    <div className='results'>
      <IonCard>
        <img src="assets/salad.jpg"/>
        <IonCardHeader>
          <IonCardTitle class='results-title'>Tossed Salad</IonCardTitle>
        </IonCardHeader>
        <IonCardContent class='results-item'>
          10 Calories | 2g Carbs | 1g Protein | 0g Fat
        </IonCardContent>
      </IonCard>
    </div>

  </IonContent>
</IonPage>
);
};

export default Tab2;
